import { Vec3 } from 'cc';
import { randomRangeInt } from 'cc';
import { Label } from 'cc';
import { Widget } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { KylinPoolManager } from '../../base/KylinPoolManager';
import { tween } from 'cc';
import { UIOpacity } from 'cc';
import { KylinUtils } from '../../base/KylinUtils';
import { KylinGameElement } from '../KylinGameElement';
import { KylinAnimationManager } from '../KylinAnimationManager';
import { sp } from 'cc';
import { KylinGameManager } from '../KylinGameManager';
import { find } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('UIKylinEntryMini')
export class UIKylinEntryMini extends Component {
    @property(Node)
    fakeNode: Node = null;
    @property(Node)
    roundTip: Node = null;
    @property(Label)
    roundText: Label = null;
    @property(Node)
    flyScoreNode: Node = null;
    @property(Node)
    flyContentNode: Node = null;
    @property(sp.Skeleton)
    kylinSp: sp.Skeleton = null;
    @property(Node)
    lightNode: Node = null;
    @property(sp.Skeleton)
    dice: sp.Skeleton = null;
    @property(Node)
    highLightNode: Node = null;

    private static _instance: UIKylinEntryMini = null;
    public static get instance() {
        return this._instance;
    }

    protected onLoad() {
        if (UIKylinEntryMini._instance) {
            this.node.destroy();
            return;
        }

        UIKylinEntryMini._instance = this;
        const widget = this.node.getComponent(Widget);
        widget.left = widget.right = widget.top = widget.bottom = 0;
        this.node.position = Vec3.ZERO;
        this.kylinSp.setCompleteListener(() => {
            this.showRound();
        });
        this.kylinSp.node.active = false;
        this.dice.node.active = false;
        this.node.active = false;
    }

    //假装进入小游戏
    public showFakeMiniGame() {
        this.node.active = true;
        this.lightNode.active = true;
        this.fakeNode.active = true;
    }

    public entryMiniGame() {
        KylinAnimationManager.instance.closeAllAni();
        this.kylinSp.node.active = true;
        this.scheduleOnce(() => {
            this.dice.node.active = true;
            this.dice.setAnimation(0, 'animation', false);
            KylinGameManager.instance.variable.dice.node.active = false;
            KylinGameManager.instance.variable.diceSpeedUp.node.active = false;
        }, 0.5);
        this.kylinSp.node.parent = this.node;
        this.kylinSp.node.setSiblingIndex(10);
        this.kylinSp.node.position = new Vec3(223, 543);
        this.kylinSp.setAnimation(0, 'animation', false);
    }

    protected onDestroy(): void {
        UIKylinEntryMini._instance = null;
    }

    public showRound() {
        this.node.active = true;
        KylinGameManager.instance.variable.miniTip.active = true;
        this.fakeNode.active = false;
        this.roundTip.active = true;
        const bg = find('bg', this.roundTip);
        bg.scale = Vec3.ONE;
        bg.getComponent(UIOpacity).opacity = 255;
        this.kylinSp.node.parent = this.roundTip;
        this.kylinSp.node.position = new Vec3(223, 563);
        this.kylinSp.node.setSiblingIndex(1);
        this.scheduleOnce(() => {
            this.showFlyScore();
        }, 2);

    }

    //进入小游戏时积分牌飞舞
    public showFlyScore() {
        this.flyScoreNode.active = true;
        const ranNum = 10;
        const ranTime = 0.3 + Math.random() * 0.2;
        this.schedule(this.createFlyObj, ranTime, ranNum, 0.1);

        this.scheduleOnce(() => {
            this.hide();
        }, ranTime * ranNum + 1);
    }

    //创建要飞舞的牌
    private createFlyObj() {
        //生成的牌随机在哪一列
        const parentIndex = randomRangeInt(0, 4);
        const element = KylinPoolManager.instance.getPoolObj(KylinPoolManager.PoolsName.gameElement);
        element.parent = this.flyContentNode.children[parentIndex === 0 ? 0 : 1];
        element.getComponent(KylinGameElement).setScoreFace();
        let endPos = new Vec3();
        if (parentIndex === 0) {
            element.position = new Vec3(0, -253);
            endPos = new Vec3(0, -1025);
        } else {
            element.position = new Vec3(0, -157);
            const pow = randomRangeInt(1, 3);
            endPos = new Vec3(318 * Math.pow(-1, pow), -870);
        }

        element.scale = Vec3.ZERO;
        const uIOpacity = element.getComponent(UIOpacity);
        uIOpacity.opacity = 0;
        const ranScale = 1 + Math.random() * 0.3;
        tween(uIOpacity).to(0.3, { opacity: 255 }).start();
        tween(element).to(0.3, { scale: new Vec3(ranScale, ranScale) })
            .call(() => {
                tween(element).to(0.3, { scale: Vec3.ONE }).start();
                KylinUtils.instance.bezierPostionTo(element, 0.3, 0, element.position.clone(), new Vec3(endPos.x, element.position.y), endPos).call(() => {
                    KylinPoolManager.instance.putPoolObj(KylinPoolManager.PoolsName.gameElement, element);
                }).start();
            })
            .start();
    }

    public hideRound() {
        this.kylinSp.node.active = false;
        this.dice.node.active = false;
        // this.roundTip.active = false;
        this.fakeNode.active = false;
        this.lightNode.active = false;
        KylinGameManager.instance.variable.entryMiniGameEffect.active = false;
        const bg = find('bg', this.roundTip);
        if (this.roundTip.activeInHierarchy) this.highLightNode.active = true;
        tween(bg).to(1, { scale: new Vec3(0.8, 0.8) }).call(() => {
            tween(bg.getComponent(UIOpacity)).to(1, { opacity: 0 }).call(() => {
                this.scheduleOnce(() => {
                    this.highLightNode.active = false;
                    this.roundTip.active = false;
                }, 2);
            }).start();
        }).start();
    }

    public hide() {
        this.roundTip.active = false;
        this.fakeNode.active = false;
        this.flyScoreNode.active = false;
        this.node.active = false;
    }
}


