import { Mask } from 'cc';
import { tween } from 'cc';
import { Vec3 } from 'cc';
import { UITransform } from 'cc';
import { v3 } from 'cc';
import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
// @ts-ignore
import { TweenFun } from "./TweenFun";
import { TweenEasing } from 'cc';
import { SpriteAtlas } from 'cc';
import { Material } from 'cc';
// @ts-ignore
import { EaseType, IRollInfo, RankType } from './Slot';
import { Label } from 'cc';
import { Font } from 'cc';
import { SpriteFrame } from 'cc';
import { SlotItem } from './SlotItem';
import { easing } from 'cc';

const { ccclass, property } = _decorator;


export interface ISlotLayerParam {
    easingIn: string;
    easingOut: string;
}

@ccclass('SlotLayer')
export class SlotLayerCopy extends Component {
    @property({ displayName: "宽度" })
    public width: number = 150;
    @property({ displayName: "高度" })
    public height: number = 150;
    @property({ displayName: "间隔" })
    public space: number = 80;
    @property({ displayName: "显示数量" })
    public count: number = 3;
    @property({ type: [SpriteAtlas], displayName: "图片库", visible: false })
    public lib: SpriteAtlas[] = [];
    @property({ displayName: "运行", group: { id: "1", name: "渲染面板" } })
    public get go() {
        return false;
    }
    public set go(v: boolean) {
        this.setup(this._lin);
    }
    rank: RankType = RankType.竖排;
    private _wheels: Array<Array<number>> = [];
    private _wheelCopy: Array<Array<number>> = [];
    public setWheel(v: Array<number>, libCount: number) {
        this._wheels[libCount] = v;
        this.copyWheel();
    }
    private copyWheel() {
        this._wheelCopy = JSON.parse(JSON.stringify(this._wheels));
    }
    public isUseBg: boolean = false;
    public bgFrame: SpriteFrame = null;
    public material: Material;
    public font: Font;
    public size: number;
    public fontVec: Vec3;
    public libCount = 0;
    public set txtSkins(v: Array<IRollInfo>) {
        this._txtSkins = v;
    }
    public autoHeight = true;
    public speed: number = 20;
    private _txtSkins: Array<IRollInfo>;
    private _normalMaterial: Material;
    private _isMove = false;
    private _isStop = false;
    private _isSlow = false;
    private _isVirtually = false;
    private _childs: SlotItem[] = [];
    private _tolHeight = 0;
    private _tolY = 0;
    private _minY = 0;
    private _time = 0;
    private _h: number;
    private _moves: boolean[] = [];
    private _resets: boolean[] = [];
    private _blurs: boolean[] = [];
    private _points: Vec3[] = [];
    private _txts: Label[] = [];
    private _juli = 0;
    private _over = 0;
    private _rolls: Array<IRollInfo>;
    private _callback: Function;
    private _stopCall: Function;
    private _lin: Array<string>;
    private _infos={pos:0};
    private _inStr: string;
    private _outStr: string;
    private _easingIn: Function = TweenFun.Quad.easeIn;
    private _easingOut: TweenEasing = "backOut";
    public set easingOut(v: string) {
        this._outStr = v;
        this._easingOut = v == "Linear" ? "linear" : (v.toLocaleLowerCase() + "Out") as TweenEasing;
    }
    public set easingIn(v: string) {
        this._inStr = v;
        this._easingIn = v == "Linear" ? TweenFun.Linear : TweenFun[v].easeIn;
    }
    public index: number;
    setup(arr?: Array<string>) {
        if (!arr) {
            console.error("没有传入初始数组");
            return;
        }
        this._lin = arr;
        this.node.removeAllChildren();
        this._childs = [];
        if (this.lib == null) return;
        let count = this.count + 1;
        let height = 0;
        let isShu = this.rank == RankType.竖排;
        let target = isShu ? this.height : this.width;
        this._h = target / 2 + this.space;
        for (let i = 0; i < count; i++) {
            var stf = this.getRndSftByArr(arr);
            var node = new Node();
            // const l=node.addComponent(Label);
            // l.string=i.toString();
            node.layer = this.node.layer;
            node.name = "item";
            var st = node.addComponent(SlotItem);
            st.column = i;
            st.layer = this.index;
            st.spriteFrame = stf;
            this.node.addChild(node);
            if (this.isUseBg) {
                st.bgSpriteFrame = this.bgFrame;
            }
            let y = isShu ? -(i * this._h) + target / 2 : i * this._h + target / 2;
            height += this._h;
            let pos = isShu ? v3(0, y, 0) : v3(y, -this.height / 2, 0);
            node.setPosition(pos);
            this._childs.push(st);
            this._points[i] = pos;
            this._moves[i] = true;
            this._normalMaterial = st.sharedMaterial;
            const txt = this.getTxt();
            txt.useSystemFont = false;
            txt.font = this.font;
            txt.fontSize = this.size;
            txt.node.layer = node.layer;
            txt.node.active = false;
            node.addChild(txt.node);
            this._txts[i] = txt;
            if(i==0)this._minY=y;
        }
        this.getComponent(Mask) || this.addComponent(Mask);
        let ui = this.getComponent(UITransform);
        ui.anchorX = isShu ? 0.5 : 0;
        if (this.autoHeight) {
            if (isShu) {
                ui.width = this.width;
                ui.height = height - this._h;
            } else {
                ui.width = height - this._h;
                ui.height = this.height;
            }
        }
        ui.anchorY = 1;
        this._tolHeight = isShu ? ui.height : ui.width;
        this._tolY = isShu ? -(this._tolHeight + target / 2) : this._tolHeight + target / 2;
    }
    private getTxt() {
        const node = new Node();
        const txt = node.addComponent(Label);
        txt.string = "0";
        node.position = this.fontVec;
        return txt;
    }
    //开始转动，传入roll数组
    public roll(arr: Array<IRollInfo>, callback: Function) {
        if (this._isMove && !this._isVirtually) return;
        this._rolls = arr;
        if (!this._isVirtually) this.init();
        this._isVirtually = false;
        this._callback = callback;
        tween(this._infos).to(1,{pos:this._infos.pos+this._childs.length*2},{easing:TweenFun.Elastic.easeOut}).call(()=>{
            this._isMove=false;
            this._callback();
        }).start();
    }
    //虚拟转盘，传入slot名字集合和积分集合
    public rollVirtually(txtSkins: Array<IRollInfo>) {
        if (this._isMove) return;
        this._isVirtually = true;
        this._txtSkins = txtSkins;
        this.init();
    }
    //停止转动
    public stop(stopCall?: Function) {
        this._isStop = true;
        this._isSlow = false;
        this._stopCall = stopCall;
    }
    //逐渐停止转动
    public stopSlow(stopCall?: Function) {
        this._time = 0;
        this._juli=this.speed;
        this._isSlow=true;
        this._stopCall = stopCall;
    }
    //获取当前点位列
    public get column() {
        let res: Node[] = [];
        for (let i = 0; i < this._childs.length - 1; i++) {
            res.push(this._childs[i].node);
        }
        return res;
    }
    private init() {
        this._juli=0;
        this._over = 0;
        this._isMove = true;
        this._isStop = false;
        this._isSlow = false;
        this._time = 0;
        this.childInit();
        // this._moves.forEach(v=>v=true);
    }
    private childInit() {
        const isShu = this.rank == RankType.竖排;
        let target = isShu ? this.height : this.width;
        for (let i = 0; i < this._moves.length; i++) {
            this._moves[i] = true;
            this._resets[i] = false;
            this._blurs[i] = false;
            let y = isShu ? -(i * this._h) + target / 2 : i * this._h + target / 2;
            let pos = v3(isShu ? 0 : y, isShu ? y : 0, 0);
            this._childs[i].node.active = true;
            this._childs[i].node.setPosition(pos);
        }
    }
    update() {
        if (!this._isMove) return;
        this._time+=1;
        for (let i = 0; i < this._childs.length; i++) {
            let one = this._childs[i];
            const isShu = this.rank == RankType.竖排;
            const h=this._h;
            const prevy=one.node.position.y;
            const y=-(this._infos.pos + i) % this._childs.length * h + this.height / 2;
            one.node.setPosition(v3(0,y,0));
            const ty = isShu ? Math.abs(prevy - this._tolY)<=5 : one.node.position.x >= this._tolY;
            if (ty&&one.node.position.y>=0) {
                if(i>0){
                    const last=this._rolls[i-1];
                    one.spriteFrame = this.lib[this.libCount].getSpriteFrame(last ? last.name.toString() : "1102");
                }else{
                    one.spriteFrame = this.getRndSft();
                }
                // const txt = isRe ? this._rolls[i].text : this.getRndTxt();
                // this.setTxt(i, txt);
            }
        }
    }
    private rollOver(){
        this._over++;
        if (this._over == this.count + 1) {
            this._isMove = false;
            this._childs[this._childs.length - 1].node.active = false;
            this._callback();
        }
    }
    private getRndTxt() {
        const rect = this._txtSkins;
        if (!rect) return '';
        return rect[Math.floor(Math.random() * rect.length)].text;
    }
    private setTxt(i: number, txt: string) {
        const index = this._txtSkins?.findIndex(v => v.name == this._childs[i].spriteFrame.name);
        this._txts[i].node.active = index > -1;
        if (index > -1) {
            this._txts[i].string = txt;
        }
    }
    getRndSft() {
        if (this._wheelCopy[this.libCount]) {
            if (this._wheelCopy[this.libCount].length == 0) {
                this.copyWheel();
            }
            return this.lib[this.libCount].getSpriteFrame(this._wheelCopy[this.libCount].shift().toString());
        } else {
            const list = this.lib[this.libCount].getSpriteFrames();
            const st = list[Math.floor(Math.random() * list.length)];
            return st;
        }
    }
    getRndSftByArr(arr: Array<string>) {
        const st = arr[Math.floor(Math.random() * arr.length)];
        return this.lib[this.libCount].getSpriteFrame(st);
    }
}


