import { _decorator, Component, Node } from 'cc';
//@ts-ignore
import { SlotLayer } from './SlotLayer';
import { Enum } from 'cc';
import { SpriteAtlas } from 'cc';
import { Material } from 'cc';
import { Font } from 'cc';
import { Vec3 } from 'cc';
import { SpriteFrame } from 'cc';
import { randomRangeInt } from 'cc';
import { KylinUtils } from '../base/KylinUtils';
const { ccclass, property } = _decorator;

export enum StopType {
    立即停止,
    陆续停止,
    立即陆续
}
export enum RankType{
    竖排,
    横排
}
export interface IRollInfo {
    name: string;//精灵名字
    text?: string;//显示金额
}
export enum EaseType {
    Linear,
    Back,
    Bounce,
    Circ,
    Cubic,
    Elastic,
    Expo,
    Quad,
    Quart,
    Quint,
    Sine,
}

Enum(StopType)
Enum(EaseType)
Enum(RankType)
@ccclass('Slot')
export class Slot extends Component {
    @property({ type: [SpriteAtlas], displayName: "图片库" })
    libs: SpriteAtlas[] = [];
    @property({ displayName: "自动高度" })
    autoHeight = true;
    @property({ type: StopType, displayName: "停止类型" })
    stopType = StopType.立即停止;
    @property({ displayName: "间隔时间", visible() { return this.stopType == StopType.陆续停止||this.stopType == StopType.立即陆续; } })
    interval: number = 0.8;
    
    @property
    private _speed = 80;
    @property({ displayName: "速度" })
    set speed(v:number){
        for (let layer of this.layers) {
            layer.speed=v;
        }
        this._speed = v;
    }
    get speed() {
        return this._speed
    }

    @property
    _easingIn=EaseType.Back;
    @property({ type: EaseType, displayName: "缓入类型" })
    set easingIn(v:EaseType){
        this._easingIn=v;
        for (let layer of this.layers) {
            layer.easingIn = EaseType[this.easingIn];
        }
    }
    get easingIn(){
        return this._easingIn;
    }
    @property
    _easingOut = EaseType.Back;
    @property({ type: EaseType, displayName: "缓出类型" })
    set easingOut(v:EaseType){
        this._easingOut=v;
        for (let layer of this.layers) {
            layer.easingOut = EaseType[this.easingOut];
        }
    }
    get easingOut(){
        return this._easingOut;
    }
    @property({ displayName: "文本位置" })
    fontVec: Vec3 = new Vec3(0, 0, 0);
    @property({ type: Font, displayName: "字体" })
    font: Font;
    @property({ displayName: "字体大小" })
    size: number = 24;
    @property({ displayName: "运动模糊" })
    isBlur: boolean = false;
    @property({ tooltip: '是否使用背景' })
    isUseBg: boolean = false;
    @property({ type: SpriteFrame, displayName: "背景图片", visible: function () { return this.isUseBg; } })
    bgFrame: SpriteFrame = null;
    @property({type:RankType})
    rank:RankType=RankType.竖排;
    @property({type:Material,visible:function(){return this.isBlur}})
    material:Material;
    @property({ displayName: "运行", group: { id: "1", name: "渲染面板" } })

    public get go() {
        return false;
    }
    public set go(v: boolean) {
        this.setup(["101", "102", "103"]);
    }
    public get layers() {
        return this._layers;
    }
    private _layers: SlotLayer[] = [];
    private _elementIds: string[] = [];
    //roll点
    public roll(arr: Array<Array<IRollInfo>>, callback: Function, callbackColumn?: Function) {
        let now = 0;
        this._layers.forEach((v, i) => {
            const interval = i * ((this.stopType == StopType.立即停止||this.stopType == StopType.立即陆续) ? 0 : this.interval);
            this.scheduleOnce(() => {
                v.roll(arr[i], () => {
                    now++;
                    if (callbackColumn) callbackColumn(i);
                    if (now == this._layers.length) {
                        callback && callback();
                    }
                });
            }, interval);
        });
    }

    public showAllLayer() {
        for (let i = 0; i < this.columnFlat.length; i++) {
            this.columnFlat[i].active = true;
        }
    }
    //虚拟roll，按钮点击后，无视网络马上开始
    public rollVirtually(txtSkins?: Array<IRollInfo>, callback?: Function) {
        this._layers.forEach((v, i) => {
            //顺序延时开始
            const interval = i * (this.stopType == StopType.立即停止 ? 0 : this.interval);
            this.scheduleOnce(() => {
                v.rollVirtually(txtSkins);
                callback && callback(i);
            }, interval)
        });
    }
    //停止转动
    public stop() {
        if (this.stopType == StopType.立即停止) {
            this._layers.forEach((v, i) => {
                v.stop();
            });
        } else {
            // this._layers.forEach((v, i) => {
            //     console.log(this.speed%i)
            //     this.scheduleOnce(() => {
            //         v.stop();
            //     },i*this.interval);
            // });
            this.stepStop(0);
        }
    }

    //逐渐停止转动
    public stopSlow(){
        
    }
    //图库切换
    public toggle(num: number, arr: Array<IRollInfo>) {
        this._layers.forEach((v) => {
            v.txtSkins = arr;
            v.libCount = num;
            // console.log(arr,',,,,,,,,,,,,,,,')
        });
    }
    private stepStop(i: number) {
        this.scheduleOnce(() => {
            this._layers[i].stop(() => {
                if (i + 1 == this._layers.length) return;
                this.scheduleOnce(() => {
                    this.stepStop(i + 1);
                }, this.interval); 
            });
        },this.interval);
    }
    //获取点位,二维格式
    public get column() {
        let res: Array<Array<Node>> = [];
        this._layers.forEach(v => {
            res.push(v.column);
        });
        return res;
    }
    //获取点位，平铺格式
    public get columnFlat() {
        let res: Array<Node> = [];
        this._layers.forEach(v => {
            res.push(...v.column);
        });
        return res;
    }
    //设置预制wheel,libCount:第几套图库
    public setWheels(wheels: Array<Array<number>>, libCount: number) {
        this._layers.forEach((v, i) => {
            v.setWheel(wheels[i], libCount)
        });
    }

    /**获取随机元素
     * @param column 每列数量
    */
    public getRandomElementIds(column?: number[]) {
        const rollInfo: IRollInfo[][] = [];
        column = column || [4, 4, 4];
        // const column = [4, 4, 4];
        const elementIds = [...this._elementIds];
        for (let i = 0; i < 3; i++) {
            rollInfo.push([]);
            const randomElement = [];
            for (let k = 0; k < 2; k++) {
                const idIndex = randomRangeInt(0, elementIds.length);
                randomElement.push(elementIds[idIndex]);
                elementIds.splice(idIndex, 1);
            }
            for (let j = 0; j < column[i]; j++) {
                const ran = randomRangeInt(0, 2);
                const elementId = randomElement[ran];
                const cardScore = 0;
                rollInfo[i].push({ name: elementId.toString(), text: KylinUtils.instance.changeString(cardScore) });
            }
        }

        return rollInfo;
    }

    public async setup(arr: Array<string>) {
        this._elementIds = arr;
        for (let i=0;i<this.node.children.length;i++) {
            const some=this.node.children[i];
            const layer = some.getComponent(SlotLayer);
            layer.index=i;
            layer.rank=this.rank;
            layer.easingIn = EaseType[this.easingIn];
            layer.easingOut = EaseType[this.easingOut];
            layer.isUseBg = this.isUseBg;
            layer.bgFrame = this.bgFrame;
            layer.material = this.material;
            layer.font = this.font;
            layer.size = this.size;
            layer.fontVec = this.fontVec;
            layer.lib = this.libs;
            layer.speed = this.speed;
            this._layers.push(layer);
            layer.autoHeight = this.autoHeight;
            layer.setup(arr);
        }
    }
}


