import { UITransform, instantiate, Vec3, tween, easing } from 'cc';
import { Prefab } from 'cc';
import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('KylinScrollParam')
class KylinScrollParam {
    @property({ type: Node, displayName: '单列元素的父节点' })
    parentNode: Node = null;
    @property({ type: Node, tooltip: '展示结果的列' })
    endNode: Node = null;
    @property({ displayName: '元素数量' })
    element: number = 0;
    @property({ displayName: '可视范围最高点' })
    maxY: number = 0;
    @property({ displayName: '元素距离' })
    distance: number = 319;
    @property({ tooltip: "元素循环出现次数" })
    repeatNum: number = 0;
    @property({ tooltip: '速度' })
    speed: number = -1;
    @property({ tooltip: '节点的高' })
    contentHeight: number = 0;

    minY: number = 0;
}

@ccclass('KylinBaseScroll')
export class KylinBaseScroll extends Component {
    @property(Prefab)
    elementPrefab: Prefab = null;
    @property({ tooltip: '父节点的宽' })
    contentWidth: number = 0;
    @property({ tooltip: '是否开启缓动' })
    isEasing: boolean = true;
    @property({ tooltip: '正常速度是每列的启动间隔' })
    intervalTime: number = 0.2;
    @property({ type: [KylinScrollParam] })
    paramList: KylinScrollParam[] = [];
    @property({ tooltip: '最大速度' })
    maxSpeed: number = 3;
    @property({ type: Node, tooltip: "结束是节点" })
    endContent: Node = null;

    private _isSpeed = false;
    public get isSpeed() {
        return this._isSpeed;
    }
    public set isSpeed(value) {
        this._index = 0;
        this._twNum = 0;
        this._singleEndMove = false;
        this._isEndMove = [];
        for (let i = 0; i < this.paramList.length; i++) {
            this._isEndMove.push(false);
        }
        this._isSpeed = value;
    }

    private _isMini = false;//是否小游戏
    public get isMini() {
        return this._isMini;
    }
    public set isMini(value) {
        this._isMini = value;
    }

    private _addSpeed = 1; //加速度

    private _isFirst = true;
    private _isStart = false;
    private _isStop = [];
    private _repeat: number[] = [];
    private _time = 0;
    private _isStopScroll = false;
    private _isStartMove = false; //开始 结束内容转动
    private _isEndMove: boolean[] = []; //结束 结束内容转动
    private _faceCb: Function = null; //换牌面回调
    private _singleEndMove = false; //不加速时的 结束 结束内容转动
    private _intervalTime = 0;
    private _endCb: Function = null;

    public addElement(cb: Function) {
        for (let i = 0; i < this.paramList.length; i++) {
            const param = this.paramList[i];
            // const height = this.elementHeight * param.element + (param.distance - this.elementHeight) * param.element;
            param.parentNode.getComponent(UITransform).setContentSize(this.contentWidth, param.contentHeight);
            param.endNode.getComponent(UITransform).setContentSize(this.contentWidth, param.contentHeight);
            param.minY = -(param.maxY + param.distance);
            for (let j = 0; j < param.element + 1; j++) {
                const element = instantiate(this.elementPrefab);
                element.parent = param.parentNode;
                element.position = new Vec3(0, param.maxY + param.distance + param.distance * j);
                // let elementNum = j - 1;
                // if (elementNum < 0) elementNum = param.element;
                element.name = j.toString();
                cb && cb(element);
            }

            for (let k = param.element - 1; k >= 0; k--) {
                const element = instantiate(this.elementPrefab);
                element.parent = param.endNode;
                element.position = new Vec3(0, param.maxY - param.distance * k);
                element.name = k.toString();
                cb && cb(element);
            }
        }
    }

    public startScroll(faceCb: Function, endCb: Function) {
        this._faceCb = faceCb;
        this._endCb = endCb;
        this.resetScroll();
        if (this.isSpeed) this._intervalTime = 0;
        else this._intervalTime = this.intervalTime;
        this._time = 0;
        this._index = 0;
        this._isStart = true;
        this._isStartMove = true;
        this._isStopScroll = false;
        this._isStop = [];
        this._repeat = [];
        this._twNum = 0;
        this._isEndMove = [];
        for (let i = 0; i < this.paramList.length; i++) {
            this._isStop.push(false);
            this._repeat.push(0);
            this._isEndMove.push(false);
        }
    }

    public stopScroll() {
        this._isStopScroll = true;
    }

    private resetScroll() {
        if (this._isFirst) { this._isFirst = false; return; }

        for (let i = 0; i < this.paramList.length; i++) {
            const param = this.paramList[i];
            param.speed = -1;
            for (let j = 0; j < param.parentNode.children.length; j++) {
                const element = param.parentNode.children[j];
                // const index = Number(element.name);
                // element.position = new Vec3(0, -param.minY + param.distance * index);
                element.position = new Vec3(0, -param.minY + param.distance * j);
            }
        }
    }

    update(deltaTime: number) {
        this.commonGame(deltaTime);
    }

    private commonGame(deltaTime: number) {
        this.moveEndElement2(deltaTime);
        if (!this._isStart) return;
        let isStop = true;
        this._time += deltaTime;

        this.moveStartElement(deltaTime);
        for (let i = 0; i < this.paramList.length; i++) {
            const param = this.paramList[i];
            if (this._time < i * this._intervalTime) continue;
            param.speed += this._addSpeed * deltaTime * 10;
            if (param.speed >= this.maxSpeed) param.speed = this.maxSpeed;
            for (let j = 0; j < param.parentNode.children.length; j++) {
                const element = param.parentNode.children[j];
                if (Math.abs(element.position.clone().y + param.minY) < 1 && this._isStop[i]) {
                    continue;
                }
                isStop = false;
                const _y = element.position.clone().y - deltaTime * param.speed * 1000;
                element.position = new Vec3(0, _y);
                if (_y < param.minY) {
                    element.position = new Vec3(0, -param.minY);
                    this._faceCb && this._faceCb(element);
                    if (j === 0 && this._isStopScroll) {
                        this._repeat[i]++;
                        if (this._repeat[i] > param.repeatNum) {
                            this._isStop[i] = true;
                            this._isEndMove[i] = true;
                            this._singleEndMove = true;
                        }
                    }
                }
            }
        }

        if (isStop) this._isStart = false;
    }

    private miniGame(deltaTime: number) {
        this.moveEndElement(deltaTime);
        if (!this._isStart) return;
        let isStop = true;
        this._time += deltaTime;

        for (let i = 0; i < this.paramList.length; i++) {
            const param = this.paramList[i];
            if (this._time < i * this.intervalTime) continue;
            param.speed += this._addSpeed * deltaTime * 10;
            if (param.speed >= this.maxSpeed) param.speed = this.maxSpeed;
            for (let j = 0; j < param.parentNode.children.length; j++) {
                const element = param.parentNode.children[j];
                if (Math.abs(element.position.clone().y + param.minY) < 1 && this._isStop[i]) {
                    continue;
                }
                isStop = false;
                const _y = element.position.clone().y - deltaTime * param.speed * 1000;
                element.position = new Vec3(0, _y);
                if (_y < param.minY) {
                    element.position = new Vec3(0, -param.minY);
                    if (j === 0) {
                        this._repeat[i]++;
                        // if (this._repeat[i] > this.repeatNum) {
                        if (this._repeat[i] > param.repeatNum) {
                            this._isStop[i] = true;
                            // this._isEndMove = true;
                        }
                    }
                }
            }
        }

        if (isStop) this._isStart = false;
    }

    //结束内容开始转动
    private moveStartElement(deltaTime) {
        if (!this._isStartMove) return;
        let isStop = false;
        let num = 0;
        for (let i = 0; i < this.paramList.length; i++) {
            const param = this.paramList[i];
            if (this._time < i * this._intervalTime) continue;
            let elementNum = 0;
            for (let j = 0; j < param.endNode.children.length; j++) {
                const element = param.endNode.children[j];
                if (element.position.clone().y > param.maxY + 150) {
                    elementNum++;
                    continue;
                }

                const _y = element.position.clone().y - deltaTime * param.speed * 1000;
                element.position = new Vec3(0, _y);
                if (_y < param.minY) {
                    element.position = new Vec3(0, -param.minY + Number(element.name) * param.distance + param.distance * 2);
                }
            }
            if (elementNum === param.element) {
                num++;
            }

            if (num === this.paramList.length) isStop = true;
        }

        if (isStop) this._isStartMove = false;
    }

    private _index = 0;
    private moveEndElement2(deltaTime) {
        if (this.isSpeed) {
            if (!this._isEndMove[this._index]) return;
            this._isEndMove[this._index] = false;
        } else {
            if (!this._singleEndMove) return;
            this._singleEndMove = false;
        }

        this.endScroll();
        this._index++;
    }

    //结束内容出结果转动
    private moveEndElement(deltaTime) {
        if (!this._isEndMove) return;
        let isStop = false;
        let num = 0;
        for (let i = 0; i < this.paramList.length; i++) {
            const param = this.paramList[i];
            if (!this._isStop[i]) continue;
            let elementNum = 0;
            for (let j = 0; j < param.endNode.children.length; j++) {
                const element = param.endNode.children[j];
                if (Math.abs(element.position.clone().y - (param.maxY - j * param.distance)) < 50) {
                    elementNum++;
                    continue;
                }

                const _y = element.position.clone().y - deltaTime * param.speed * 1000;
                element.position = new Vec3(0, _y);
            }
            if (elementNum === param.element) {
                num++;
            }

            if (num === this.paramList.length) isStop = true;
        }

        // if (isStop) this._isEndMove = false;
    }

    private _twNum = 0;
    private endScroll() {
        const param = this.paramList[this._index];
        for (let j = 0; j < param.endNode.children.length; j++) {
            const element = param.endNode.children[j];
            const pos = new Vec3(0, param.maxY - param.distance * j);
            tween(element).to(0.7, { position: pos }, { easing: easing.backOut }).call(() => {
                this._twNum++;
                if (this._twNum === 10) {
                    this._endCb && this._endCb();
                }
            }).start();
        }
    }
}


