import { tween } from 'cc';
import { Tween } from 'cc';
import { Label } from 'cc';
import { RichText } from 'cc';
import { Sprite } from 'cc';
import { SpriteFrame } from 'cc';
import { Vec3 } from 'cc';
import { UITransform } from 'cc';
import { macro } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { KylinUtils } from '../base/KylinUtils';
import { KylinPoolManager } from '../base/KylinPoolManager';
import { ParticleSystem } from 'cc';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
const { ccclass, property } = _decorator;

//<img src='101'/>
const NoticeMsg = ['ql_broadcast1', 'ql_broadcast2', 'ql_broadcast3'];
@ccclass('KylinNoticeManager')
export class KylinNoticeManager extends Component {
    @property(Sprite)
    noticeBg: Sprite = null;
    @property(RichText)
    msgLab: RichText = null;
    @property(UITransform)
    maskTrans: UITransform = null;
    @property(Node)
    scoreNode: Node = null;
    @property(Sprite)
    winSpr: Sprite = null;
    @property(Label)
    winScoreLab: Label = null;
    @property([SpriteFrame])
    winFrames: SpriteFrame[] = [];
    @property([SpriteFrame])
    winTextFrames: SpriteFrame[] = [];
    @property(Node)
    bigPrizeEffect: Node = null;
    @property(Node)
    prizeEffect: Node = null;

    private static _instance: KylinNoticeManager = null;
    public static get instance() {
        return this._instance;
    }

    private _noticeIndex = 2;
    private _msgTween: Tween<Node> = null;
    private _winScore = 0;
    private _isShow = false;
    private _miniWinScore = 0;
    public set miniWinScore(value) {
        this._miniWinScore = value;
    }

    protected onLoad(): void {
        if (KylinNoticeManager._instance) {
            this.node.destroy();
            return;
        }

        KylinNoticeManager._instance = this;
        this.showNotice();
    }

    //==============结算分数===================
    public balanceScore(betScore: number, winScore: number, callback: Function = null) {
        if (!winScore) {
            callback && callback();
            return;
        }
        this.scoreNode.active = true;
        this.msgLab.node.active = false;
        AudioMgr.inst.playOneShot("audio/AC_sound_8001_slotqilin_card_integral", 1, ModuleDef.SLOTS_KYLIN);
        const mult = winScore / betScore;
        if (mult > 10) {
            this.bigPrizeEffect.children.forEach((v) => {
                v.getComponent(ParticleSystem).stop();
            });
            this.bigPrizeEffect.children.forEach((v) => {
                v.getComponent(ParticleSystem).play();
            });
            if (mult < 100) {
                this.noticeBg.spriteFrame = this.winFrames[1];
            } else {
                this.noticeBg.spriteFrame = this.winFrames[2];
            }
        } else {
            this.noticeBg.spriteFrame = this.winFrames[0];
            this.prizeEffect.children.forEach((v) => {
                v.getComponent(ParticleSystem).stop();
            });
            this.prizeEffect.children.forEach((v) => {
                v.getComponent(ParticleSystem).play();
            });
        }

        if (mult >= 5) KylinUtils.instance.IncreaseNumber(this.winScoreLab, 0, winScore, { prefix: 'win ', timer: 1 }).start();
        else this.winScoreLab.string = 'win ' + KylinUtils.instance.changeString((winScore).toFixed(2));

        tween(this.node).to(.2, { scale: new Vec3(1.2, 1.2, 1.2) })
            .delay(.1)
            .to(.2, { scale: Vec3.ONE })
            .call(() => {
                callback && callback();
            })
            .union()
            .start();
    }

    protected onDestroy(): void {
        KylinNoticeManager._instance = null;
    }

    public addText(twNode: Node, score: number, betScore: number, callback: Function) {
        let cPos = new Vec3();
        const txtPos = twNode.position.clone();
        if (txtPos.x < 0) cPos = twNode.worldPosition.clone().add3f(this.winScoreLab.node.worldPosition.clone().x / 2, -100, 0);
        else cPos = twNode.worldPosition.clone().subtract3f(this.winScoreLab.node.worldPosition.clone().x / 2, 100, 0);
        this.scheduleOnce(() => {
            AudioMgr.inst.playOneShot("audio/AC_sound_8001_slotqilin_card_integral", 1, ModuleDef.SLOTS_KYLIN);
        }, 0.2);

        KylinUtils.instance.bezierTo(twNode, 0.5, 0.1, twNode.worldPosition.clone(), cPos, this.winScoreLab.node.worldPosition.clone()).call(() => {
            this.scoreNode.active = true;
            this.msgLab.node.active = false;
            KylinPoolManager.instance.putPoolObj(KylinPoolManager.PoolsName.prizeText, twNode);
            this._miniWinScore += score;
            this.prizeEffect.children.forEach((v) => {
                v.getComponent(ParticleSystem).stop();
            });
            this.prizeEffect.children.forEach((v) => {
                v.getComponent(ParticleSystem).play();
            });
            this.winScoreLab.string = 'win ' + KylinUtils.instance.changeString(this._miniWinScore.toFixed(2));
            const mult = this._miniWinScore * 100 / betScore;
            if (mult >= 0 && mult <= 10) {
                this.noticeBg.spriteFrame = this.winFrames[0];
            } else if (mult > 10 && mult <= 100) {
                this.noticeBg.spriteFrame = this.winFrames[1];
            } else {
                this.noticeBg.spriteFrame = this.winFrames[2];
            }
            tween(this.scoreNode).to(0.2, { scale: new Vec3(1.2, 1.2) })
                .to(0.2, { scale: Vec3.ONE })
                .call(() => {
                    callback && callback();
                })
                .start();
        }).start();
    }

    public resetNoticeBg() {
        this.noticeBg.spriteFrame = this.winFrames[0];
    }

    //================轮播===================
    public showNotice() {
        this.schedule(this.setNotice, 7, macro.REPEAT_FOREVER, .1);
    }

    private setNotice() {
        this._msgTween && (this._msgTween.stop(), this._msgTween = null);
        this.msgLab.string = LanguageData.getLangByID(NoticeMsg[this._noticeIndex]);
        this.scheduleOnce(() => {
            const size = this.msgLab.getComponent(UITransform).contentSize;
            const maskWidth = this.maskTrans.contentSize.width;
            if (size.width > maskWidth) {
                this.msgLab.node.position = new Vec3((size.width - maskWidth) / 2);
                this.scheduleOnce(() => {
                    tween(this.msgLab.node).removeSelf();
                    this._msgTween = tween(this.msgLab.node).to(7, { position: new Vec3(-size.width, 0) });
                    this._msgTween.start();
                }, 1);

            }
            else this.msgLab.node.position = Vec3.ZERO;
        }, 1 / 60);
        this._noticeIndex++;
        if (this._noticeIndex >= NoticeMsg.length) this._noticeIndex = 0;
    }

    public stopNotice() {
        this._msgTween && (this._msgTween.stop(), this._msgTween = null);
    }

    public hideScoreNode() {
        this._miniWinScore = 0;
        this.scoreNode.active = false;
        this.msgLab.node.active = true;
        this.resetNoticeBg();
    }
}


