import { ITweenOption } from 'cc';
import { Tween, Label, easing, tween, randomRangeInt, Vec3 } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { ResPlaySlots, SlotsKylinResult } from 'db://assets/module_basic/shared/protocols/public/slots/PtlPlaySlots';
import { SlotKylinColums, SlotKylinElement, SlotKylinStartGameReq } from '../data/KylinGameData';
import { ApiReturn } from 'tsrpc-browser';
import { Num } from 'db://assets/core_tgx/framework/util/Num';
import { ParticleSystem } from 'cc';
import { CurveRange } from 'cc';
const { ccclass, property } = _decorator;

export interface IIncresaseData {
    prefix?: string, //前缀
    timer?: number, //缓动时间，默认5秒
    place?: number, //小数位， 默认2位
    callback?: Function, //回调
    easing?: ITweenOption,
    tag?: number
}

const ElementTable = {
    "kylin": 201,
    "seal": 101,
    "boat": 102,
    "frog": 103,
    "jar": 104,
    "crown": 105,
    "token": 106,
}

const LineCount = {
    1: [0, 0, 0],
    2: [0, 1, 0],
    3: [0, 1, 1],
    4: [1, 1, 0],
    5: [1, 1, 1],
    6: [1, 2, 1],
    7: [1, 2, 2],
    8: [2, 2, 1],
    9: [2, 2, 2],
    10: [2, 3, 2],
}
@ccclass('KylinUtils')
export class KylinUtils extends Component {
    private static _instance: KylinUtils = null;
    public static get instance() {
        if (!this._instance) {
            this._instance = new KylinUtils();
        }

        return this._instance;
    }
    public numberTween: Tween<any> = null;

    /**
     * 返回的数据转换
     * @param data 
     * @param bet 下注额
     */
    conver(data: ApiReturn<ResPlaySlots>, bet: number) {
        const result: SlotKylinStartGameReq = {
            betScore: 0,
            code: 200,
            main: {
                colums: [],
                hitType: 0,
                lines: [],
                winScore: 0,
            },
            mini: {
                list: [],
                winScore: 0,
            },
            winScore: 0
        };
        if (data.isSucc) {
            result.code = 200;
            result.betScore = bet;
            result.winScore = Num.toThousandsBack(data.res.payout);
            if (data.res.results && data.res.results.length < 8) {
                //连线游戏数据
                result.mini = null;
                result.main.winScore = Num.toThousandsBack(data.res.payout);
                for (let i = 0; i < 3; i++) {
                    //初始化连线游戏的数据
                    result.main.colums.push({ elements: [] });
                    let columsCount = 3;
                    if (i === 1) {
                        columsCount = 4;
                    }
                    for (let j = 0; j < columsCount; j++) {
                        result.main.colums[i].elements.push({ elementId: 302, cardScore: 0 }); //初始化空位置
                    }
                }
                const res = data.res.results[0];
                result.main.winScore = Num.toThousandsBack(res.payout);
                let scoreNum = 0; //积分个数
                //所有的元素符号
                for (let i = res.symbols.length - 1; i >= 0; i--) {
                    if (res.symbols[i].id === 'null') continue; //空位置，跳过
                    //不存在在表格中，视为积分牌
                    const reel = res.symbols[i].coordinate.reel - 1; //列号
                    let row = 0; //行号
                    if (reel === 1) row = 4 - res.symbols[i].coordinate.row; //第二列的行号
                    else row = 3 - res.symbols[i].coordinate.row;
                    if (!ElementTable[res.symbols[i].id]) {
                        //移除所有的 '倍' 字符
                        const mult = res.symbols[i].id.replace('倍', '');
                        //在对应的列中存入数据
                        const payout = res.symbols[i].payout ? Num.toThousandsBack(res.symbols[i].payout) : Num.toThousandsBack(mult) * bet;
                        result.main.colums[reel].elements[row] = { elementId: 301, cardScore: payout };
                        scoreNum++;
                    } else {
                        result.main.colums[reel].elements[row] = { elementId: ElementTable[res.symbols[i].id], cardScore: 0 };
                    }
                }
                if (res.paylines.length > 0) {
                    result.main.hitType = 1; //线路中奖
                    //所有的中奖线
                    for (let i = 0; i < res.paylines.length; i++) {
                        const payline = res.paylines[i];
                        const lineNo = parseInt(payline.id); //线号
                        const lineScore = Num.toThousandsBack(payline.payout); //线得分
                        result.main.lines.push({ lineNo: lineNo, lineScore: lineScore, line: LineCount[lineNo] });
                    }
                } else {
                    result.main.hitType = 0; //没有中奖
                }

                if (scoreNum >= 5) {
                    result.main.hitType = 2; //有5个积分牌,积分中奖
                }
            } else {
                //幸运游戏数据
                result.main = null;
                for (let i = 0; i < data.res.results.length; i++) {
                    const res = data.res.results[i];
                    let colums: SlotKylinColums[] = [];
                    for (let k = 0; k < 3; k++) {
                        colums.push({ elements: [] });
                        let columsCount = 3;
                        if (k === 1) {
                            columsCount = 4;
                        }

                        for (let l = 0; l < columsCount; l++) {
                            colums[k].elements.push({ elementId: 302, cardScore: 0 }); //初始化空位置
                        }
                    }
                    const payout = res.payout ? Num.toThousandsBack(res.payout) : 0;
                    for (let j = res.symbols.length - 1; j >= 0; j--) {
                        if (res.symbols[j].id === 'null' || res.symbols[j].id === 'empty') continue; //空位置，跳过

                        const reel = res.symbols[j].coordinate.reel - 1; //列号
                        let row = 0; //行号
                        if (reel === 1) row = 4 - res.symbols[j].coordinate.row; //第二列的行号
                        else row = 3 - res.symbols[j].coordinate.row; //第一列的行号

                        const mult = res.symbols[j].id.replace('倍', '');
                        //在对应的列中存入数据
                        const cardScore = res.symbols[j].payout ? Num.toThousandsBack(res.symbols[j].payout) : Num.toThousandsBack(mult) * bet;
                        colums[reel].elements[row] = { elementId: 301, cardScore: cardScore };
                    }
                    const miniList = {
                        colums: colums,
                        hitType: 2,
                        lines: [],
                        winScore: payout,
                    }
                    result.mini.list.push(miniList);
                    result.mini.winScore = Num.toThousandsBack(data.res.payout);
                }
            }
        } else {
            result.code = 500;
        }

        return result;
    }

    /**
    * 递归旋转、缩放子节点下粒子组件的节点
    * @param root 要查找的节点
    * @param componentType 组件类型
    */
    setNodesByComponent(root: Node, angle: number = 90, scale: Vec3 = new Vec3(0.75, 0.75, 0.75)) {
        // 检查当前节点是否包含指定类型的组件
        const comp = root.getComponent(ParticleSystem);
        if (comp) {
            // const deg = angle * (Math.PI / 180);
            // if (comp.startRotationZ.mode === CurveRange.Mode.Constant) {
            //     comp.startRotationZ.constant = deg;
            // } else if (comp.startRotationZ.mode === CurveRange.Mode.TwoConstants) {
            //     comp.startRotationZ.constantMin += deg;
            //     comp.startRotationZ.constantMax += deg;
            // }
            comp.node.scale = scale.clone().multiply(comp.node.scale.clone());
        }

        root.children.forEach(child => this.setNodesByComponent(child, angle, scale));
    }

    /**
     * 数字增长动画
     * @param startNum 开始值
     * @param endNum 目标值
     * @param textCom 文本组件
     * @param param 
     * 时间默认3秒
     * 小数位默认2位
     */
    public IncreaseNumber(textCom: Label, startNum: number, endNum: number, param?: IIncresaseData) {
        // this.numberTween && this.numberTween.stop();
        if (!param) {
            param = {
                prefix: '', //前缀
                timer: 3, //缓动时间，默认3秒
                place: 2, //小数位， 默认2位
                easing: { easing: easing.quintOut },
                tag: 1
            }
        }
        const place = param.place ? param.place : 2;
        const timer = param.timer ? param.timer : 3;
        const prefix = param.prefix ? param.prefix : '';
        const tags = param.tag ? param.tag : 1;
        const pEasing = param.easing ? param.easing : { easing: easing.quintOut };
        const dis = endNum - startNum;
        if (dis < 0) {
            // console.log("起始值比较小", startNum, '-', endNum);
            return this.ReceduNumber(textCom, startNum, endNum, param);
        }
        const length = dis * Math.pow(10, place);
        const obj = { number: 0 };
        textCom.string = prefix + Num.exchangeToThousands(1, startNum);
        this.numberTween = tween(obj).to(timer, { number: Math.round(length) }, {
            progress: (start, end, current, ratio) => {
                //startNum + start + (end - start) * ratio
                // textCom.string = prefix + (Math.floor(current * Math.pow(10, place)) / Math.pow(10, place));
                textCom.string = prefix + Num.exchangeToThousands(1, ((startNum * Math.pow(10, place) + Math.ceil(start + (end - start) * ratio)) / Math.pow(10, place)));
                return (startNum * Math.pow(10, place) + start + (end - start) * ratio) / Math.pow(10, place);
            }, easing: easing.linear
        }).call(() => {
            textCom.string = prefix + Num.exchangeToThousands(1, endNum);
            param.callback && param.callback();
        }).tag(tags);
        return this.numberTween;
    }

    /**
     * 数字减少动画
     * @param startNum 开始值
     * @param endNum 目标值
     * @param textCom 文本组件
     * @param param 
     * 时间默认2秒
     * 小数位默认2位
     */
    public ReceduNumber(textCom: Label, startNum: number, endNum: number, param?: IIncresaseData) {
        this.numberTween && this.numberTween.stop();
        if (!param) {
            param = {
                prefix: '', //前缀
                timer: 2, //缓动时间，默认2秒
                place: 2, //小数位， 默认2位
            }
        }
        const place = param.place ? param.place : 2;
        const timer = param.timer ? param.timer : 2;
        const prefix = param.prefix ? param.prefix : '';
        const tag = param.tag ? param.tag : 2;
        const dis = Math.abs(endNum - startNum);
        // if (dis < 0) return console.error('结束值必须大于开始值');
        const length = dis * Math.pow(10, place);
        const obj = { number: 0 };
        textCom.string = prefix + this.changeString(startNum.toFixed(2));
        this.numberTween = tween(obj).to(timer, { number: length }, {
            progress: (start, end, current, ratio) => {
                //startNum + start + (end - start) * ratio
                // textCom.string = prefix + (Math.floor(current * Math.pow(10, place)) / Math.pow(10, place));
                textCom.string = prefix + this.changeString(((startNum * Math.pow(10, place) - Math.ceil(start + (end - start) * ratio)) / Math.pow(10, place)).toFixed(2));
                return (startNum * Math.pow(10, place) - start + (end - start) * ratio) / Math.pow(10, place);
            }, easing: easing.quintOut
        }).call(() => {
            textCom.string = prefix + this.changeString(endNum.toFixed(2));
            param.callback && param.callback();
        }).tag(tag);
        return this.numberTween;
    }

    /**
     * 返回不在数组中的最小（包含）和最大（包含）之间的随机值
     * @argument 没优化，最大最小值不宜隔太大
     * @param min 随机最小值
     * @param max 随机最大值
     * @param arr 参考数组
     * @returns 数组内全包含则返回-1
     */
    public getRandomNumberNotInArray(min: number, max: number, arr: number[]) {
        //必包含
        // if (max - min + 1 === arr.length) return -1;
        let notInArrayNum: number[] = [];
        for (let i = min; i <= max; i++) {
            //取所有不在数组中的值
            if (!arr.includes(i)) {
                notInArrayNum.push(i);
            }
        }
        if (notInArrayNum.length <= 0) return -1;

        const index = randomRangeInt(0, notInArrayNum.length);
        return notInArrayNum[index];
    }

    /**
     * 数额转换小数点转换为逗号（.=>,）
     * @param str 要转换的字符串
     * @returns 转换后的字符串
     */
    public changeString(str: string | number, language: string = 'en') {
        let conStr = ''; //转换后的数额
        switch (language) {
            case 'pt':
                conStr = str.toString().replace(".", ",");
                break;
            case 'PT':
                conStr = str.toString().replace(".", ",");
                break;
            case 'en':
                conStr = str.toString();
            case 'EN':
                conStr = str.toString();
                break;
        }

        return conStr;
    }

    //字符串转换位number，
    public toNumber(str: string | number, language: string = 'en') {
        let conStr = ''; //转换后的数额
        switch (language) {
            case 'pt':
                conStr = str.toString().replace(",", ".");
                break;
            case 'en':
                conStr = str.toString();
                break;
        }

        return Number(conStr);
    }

    /**
     * @desc 三阶贝塞尔-世界坐标
     * @param {any} target 运动目标
     * @param {number} duration tween时长
     * @param {Vec3} p1 起点坐标
     * @param {Vec3} cp 控制点
     * @param {Vec3} p2 终点坐标
     * @param {object} opts 
     * @returns {any} 返回一个tween动画，启用需要start()
     */
    public bezierTo(target: any, duration: number, delay: number, p1: Vec3, cp: Vec3, p2: Vec3, opts?: any): Tween<any> {
        opts = opts || Object.create(null);
        let twoBezier = (t: number, p1: Vec3, cp: Vec3, p2: Vec3) => {
            let x = (1 - t) * (1 - t) * p1.x + 2 * t * (1 - t) * cp.x + t * t * p2.x;
            let y = (1 - t) * (1 - t) * p1.y + 2 * t * (1 - t) * cp.y + t * t * p2.y;
            let z = (1 - t) * (1 - t) * p1.z + 2 * t * (1 - t) * cp.z + t * t * p2.z;
            return new Vec3(x, y, z);
        };
        opts.onUpdate = (_arg: Vec3, ratio: number) => {
            //移动前位置
            // const beforPos = target.position.clone();
            target.worldPosition = twoBezier(ratio, p1, cp, p2);
        };
        return tween(target)
            .delay(delay)
            .to(duration, {}, opts);
    }

    /**
     * @desc 三阶贝塞尔-本地坐标
     * @param {any} target 运动目标
     * @param {number} duration tween时长
     * @param {Vec3} p1 起点坐标
     * @param {Vec3} cp 控制点
     * @param {Vec3} p2 终点坐标
     * @param {object} opts 
     * @returns {any} 返回一个tween动画，启用需要start()
     */
    public bezierPostionTo(target: any, duration: number, delay: number, p1: Vec3, cp: Vec3, p2: Vec3, opts?: any): Tween<any> {
        opts = opts || Object.create(null);
        let twoBezier = (t: number, p1: Vec3, cp: Vec3, p2: Vec3) => {
            let x = (1 - t) * (1 - t) * p1.x + 2 * t * (1 - t) * cp.x + t * t * p2.x;
            let y = (1 - t) * (1 - t) * p1.y + 2 * t * (1 - t) * cp.y + t * t * p2.y;
            let z = (1 - t) * (1 - t) * p1.z + 2 * t * (1 - t) * cp.z + t * t * p2.z;
            return new Vec3(x, y, z);
        };
        opts.onUpdate = (_arg: Vec3, ratio: number) => {
            //移动前位置
            // const beforPos = target.position.clone();
            target.position = twoBezier(ratio, p1, cp, p2);
        };
        return tween(target)
            .delay(delay)
            .to(duration, {}, opts);
    }

    //==================================分帧加载===============================================
    public delayTime = 0.2;
    public startLoad(callback: Function, length: number, ...data: any[]) {
        this.framingLoad(callback, length, data);
    }

    /**
     * 获取生成子节点的Generator
     */
    private *_getItemGenerator(callback: Function, length: number, data: any) {
        if (data.length > 0) {
            const isSub = data[data.length - 1];
            if (typeof (isSub) === "boolean" && isSub) {
                for (let i = length - 1; i >= 0; i--) {
                    yield callback(i, data);
                }
            } else {
                for (let i = 0; i < length; i++) {
                    yield callback(i, data);
                }
            }
        } else {
            for (let i = 0; i < length; i++) {
                yield callback(i, data);
            }
        }

    }

    /**
     * 实现分帧加载
     */
    async framingLoad(callback: Function, length: number, data: any) {
        await this.executePreFrame(this._getItemGenerator(callback, length, data), 1);
    }

    /**
     * 分帧执行 Generator 逻辑
     *
     * @param generator 生成器
     * @param duration 持续时间（ms）
     *          每次执行 Generator 的操作时，最长可持续执行时长。
     *          假设值为8ms，那么表示1帧（总共16ms）下，分出8ms时间给此逻辑执行
     */
    private executePreFrame(generator: Generator, duration: number) {
        return new Promise((resolve, reject) => {
            let gen = generator;
            // 创建执行函数
            let execute = () => {

                // 执行之前，先记录开始时间戳
                let startTime = new Date().getTime();
                this.scheduleOnce(() => {
                    // 然后一直从 Generator 中获取已经拆分好的代码段出来执行
                    for (let iter = gen.next(); ; iter = gen.next()) {

                        // 判断是否已经执行完所有 Generator 的小代码段
                        // 如果是的话，那么就表示任务完成
                        if (iter == null || iter.done) {
                            resolve(true);
                            return;
                        }

                        // 每执行完一段小代码段，都检查一下是否
                        // 已经超过我们分配给本帧，这些小代码端的最大可执行时间
                        if (new Date().getTime() - startTime > duration) {

                            // 如果超过了，那么本帧就不在执行，开定时器，让下一帧再执行
                            this.scheduleOnce(() => {
                                execute();
                            });
                            return;
                        }
                    }
                }, this.delayTime);
            };

            // 运行执行函数
            execute();
        });
    }
}


