export interface SlotKylinStartGameReq {
    betScore: number,
    winScore: number,
    code: number,
    main: SlotKylinBody,
    mini: SlotKylinMini
}

export interface SlotKylinBody {
    colums: SlotKylinColums[],
    lines: SlotKylinLine[],
    winScore: number,
    hitType: number, //中奖类型 0-未中奖 1-线路中奖 2-积分中奖
}

export interface SlotKylinColums {
    elements: SlotKylinElement[],
}

export interface SlotKylinElement {
    elementId: number, //301为积分牌
    cardScore: number, //积分牌
}

export interface SlotKylinMini {
    list: SlotKylinBody[],
    winScore: number,
}

export interface SlotKylinLine {
    line: number[],
    lineNo: number,
    lineScore: number
}

export interface ILineData {
    lineWheel: number[];
}


