import { _decorator, Component, Node } from 'cc';
import { ILineData, KylinGameData } from '../data/KylinGameData';
import { KylinGameManager } from './KylinGameManager';
import { KylinPoolManager } from '../base/KylinPoolManager';
import { Prefab } from 'cc';
import { view } from 'cc';
import { UITransform } from 'cc';
import { Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('KylinSceneManager')
export class KylinSceneManager extends Component {
    @property(Prefab)
    prizeTextPre: Prefab = null;
    @property(Prefab)
    gameElementPre: Prefab = null;
    @property(Prefab)
    scoreEffectPre: Prefab = null;
    @property(Prefab)
    cloudPrefab: Prefab = null;
    @property(Prefab)
    liuxing: Prefab = null;
    @property({ type: UITransform, tooltip: "游戏的主节点" })
    gameMainTran: UITransform = null;

    private static _instance: KylinSceneManager = null;
    public static get instance() {
        return this._instance;
    }
    public betScore: number[] = [];
    public elementOdd: number[] = []; //不除100
    public lineCount: number = 0;
    public fakeMiniRate: number = 0; //假装要进小游戏的概率，万分比，/10000
    public pointScores: number[] = [];
    public wheel: ILineData[] = [];
    public wheelData: number[][] = [];
    public miniWheelData: number[][] = [];
    public isOpenVideo: boolean = true;
    public isFirstEnty: boolean = true;
    private _isFirst: boolean = true;

    onLoad() {
        if (KylinSceneManager._instance) {
            this.node.destroy();
            return;
        }

        KylinSceneManager._instance = this;
        this.gameSetup();
    }

    reconnect(): void {
        KylinGameManager.instance.reconnect();
    }

    protected onDisable(): void {
        KylinPoolManager.instance = null;
        KylinSceneManager._instance = null;
    }

    private gameSetup() {
        //适配
        // const v = view.getVisibleSize();
        // const scaleV = v.width / this.gameMainTran.height;
        // this.gameMainTran.node.scale = new Vec3(scaleV, scaleV, 1);


        KylinPoolManager.instance.createPool({ name: KylinPoolManager.PoolsName.prizeText, prefab: this.prizeTextPre, initCount: 12 }).then(() => {
            console.log(`创建对象池${KylinPoolManager.PoolsName.prizeText}完成`);
        }).catch((e) => {
            console.log(`创建对象池${KylinPoolManager.PoolsName.prizeText}失败`);
        });

        KylinPoolManager.instance.createPool({ name: KylinPoolManager.PoolsName.gameElement, prefab: this.gameElementPre, initCount: 12 }).then(() => {
            console.log(`创建对象池${KylinPoolManager.PoolsName.gameElement}完成`);
        }).catch((e) => {
            console.log(`创建对象池${KylinPoolManager.PoolsName.gameElement}失败`);
        });

        KylinPoolManager.instance.createPool({ name: KylinPoolManager.PoolsName.scoreEffect, prefab: this.scoreEffectPre, initCount: 12 }).then(() => {
            console.log(`创建对象池${KylinPoolManager.PoolsName.scoreEffect}完成`);
        }).catch((e) => {
            console.log(`创建对象池${KylinPoolManager.PoolsName.scoreEffect}失败`);
        });

        KylinPoolManager.instance.createPool({ name: KylinPoolManager.PoolsName.cloud, prefab: this.cloudPrefab, initCount: 10 }).then(() => {
            console.log(`创建对象池${KylinPoolManager.PoolsName.cloud}完成`);
        }).catch((e) => {
            console.log(`创建对象池${KylinPoolManager.PoolsName.cloud}失败`);
        });

        KylinPoolManager.instance.createPool({ name: KylinPoolManager.PoolsName.liuxing, prefab: this.liuxing, initCount: 10 }).then(() => {
            console.log(`创建对象池${KylinPoolManager.PoolsName.liuxing}完成`);
        }).catch((e) => {
            console.log(`创建对象池${KylinPoolManager.PoolsName.liuxing}失败`);
        });

        // this.events.put(KylinGameData.MessgeDefine.Response.GameScene + store.room.id, this.initGameScene.bind(this));

    }

    public startGame() {
        this.initGameScene({
            betScore: [100, 200, 300, 400, 500, 600, 700, 800, 900], elementOdd: { 101: 100, 102: 50, 103: 10, 104: 5, 105: 3, 106: 2, 301: 2, 201: 200 }, lineCount: 10, fakeMiniRate: 10,
            pointScores: [100, 300, 500, 700, 900, 1100], mainWheel: [{ lineWheel: [101, 102, 103, 104, 105, 106, 105, 104, 103, 102, 101] }, { lineWheel: [101, 102, 103, 104, 105, 106, 105, 104, 103, 102, 101] }, { lineWheel: [101, 102, 103, 104, 105, 106, 105, 104, 103, 102, 101] }],
            miniWheel: [{ lineWheel: [302, 302, 302, 302, 302, 302, 302, 302, 302, 302, 302] }, { lineWheel: [302, 302, 302, 302, 302, 302, 302, 302, 302, 302, 302] }, { lineWheel: [302, 302, 302, 302, 302, 302, 302, 302, 302, 302, 302] }]
        })
    }

    private initGameScene(res) {
        console.log('---麒麟--', res);
        this.betScore = res.betScore;
        this.elementOdd = res.elementOdd;
        this.lineCount = res.lineCount;
        this.fakeMiniRate = res.fakeMiniRate;
        this.pointScores = res.pointScores;
        this.wheel = res.mainWheel;
        this.setWheelData(res.miniWheel);
        if (this._isFirst) {
            this._isFirst = false;
            KylinGameManager.instance.initUI();
        }
    }

    private setWheelData(miniWheel: ILineData[]) {
        for (let i = 0; i < this.wheel.length; i++) {
            this.wheelData[i] = this.wheel[i].lineWheel;
        }

        for (let i = 0; i < miniWheel.length; i++) {
            this.miniWheelData[i] = [];
            for (let j = 0; j < miniWheel[i].lineWheel.length; j++) {
                let id = miniWheel[i].lineWheel[j] ? miniWheel[i].lineWheel[j] : 302;
                this.miniWheelData[i].push(id);
            }
        }
    }
}


