import { _decorator, Component, Node } from 'cc';
import { Label } from 'cc';
import { UITransform } from 'cc';
import { Tween } from 'cc';
import { tween } from 'cc';
import { Vec3 } from 'cc';
import { IIncresaseData, KylinUtils } from '../base/KylinUtils';
import { Sprite } from 'cc';
import { SpriteFrame } from 'cc';
import { sp } from 'cc';
import { KylinPoolManager } from '../base/KylinPoolManager';
import { randomRangeInt } from 'cc';
import { random } from 'cc';
import { ParticleSystem } from 'cc';
import { Slot } from '../SlotScript/Slot';
import { PubButton } from '../base/PubButton';
const { ccclass, property, type } = _decorator;

@ccclass('KylinGameVariable')
export class KylinGameVariable {
    @property({ type: PubButton, displayName: '开始按钮' })
    btnStart: PubButton = null;
    @property({ type: PubButton, displayName: '加速按钮' })
    btnTurbo: PubButton = null;
    @property({ type: PubButton, displayName: '减按钮' })
    btnSub: PubButton = null;
    @property({ type: PubButton, displayName: '加按钮' })
    btnAdd: PubButton = null;
    @property({ type: PubButton, displayName: '自动按钮' })
    btnAuto: PubButton = null;
    @property({ type: PubButton, displayName: '停止自动按钮' })
    btnStop: PubButton = null;
    @property(Label)
    autoText: Label = null;

    @property({ type: Label, displayName: '积分' })
    scoreText: Label = null;
    @property({ type: Label, displayName: '下注额' })
    betText: Label = null;
    @property({ type: Label, displayName: '赢的分数' })
    winText: Label = null;
    @property({ type: Label, tooltip: '加速按钮提示' })
    turboTip: Label = null;
    @property({ type: Node, tooltip: '放置所有二级界面' })
    panelParentNode: Node = null;

    @property(Slot)
    scroll: Slot = null;
    @property(Slot)
    bgScroll: Slot = null;
    @property(Node)
    otherNode: Node = null;
    @property(UITransform)
    bottomBg: UITransform = null;
    @property(UITransform)
    bottomBg2: UITransform = null;
    @property(Node)
    mainBtn: Node = null;
    @property(Node)
    startRefresh: Node = null; //开始按钮旋转处
    @property([Node])
    entryMiniAniNode: Node[] = [];
    @property(Node)
    roundNode: Node = null;
    @property(Label)
    roundText: Label = null;
    @property(Sprite)
    turntableBg: Sprite = null;
    @property(Node)
    miniTip: Node = null;
    @property([SpriteFrame])
    turntableFrames: SpriteFrame[] = [];
    @property(Node)
    diceNode: Node = null;
    @property(sp.Skeleton)
    dice: sp.Skeleton = null;
    @property(sp.Skeleton)
    diceSpeedUp: sp.Skeleton = null;
    @property(Node)
    topBg: Node = null;
    @property(Sprite)
    btnVideo: Sprite = null;
    @property([SpriteFrame])
    cloudFrames: SpriteFrame[] = [];
    @property([SpriteFrame])
    videoFrames: SpriteFrame[] = [];
    @property(Node)
    videoMask: Node = null;
    //=======特效============
    @property([Node])
    startFrameList: Node[] = [];
    @property(Node)
    entryMiniGameEffect: Node = null;
    @property(Node)
    miniGameBgEffect: Node = null;
    @property(Node)
    diceEffect: Node = null;
    @property(Node)
    effectParent: Node = null;
    @property(ParticleSystem)
    startEffectFront: ParticleSystem = null;
    @property(ParticleSystem)
    startEffectAfter: ParticleSystem = null;
    @property({ type: Node, tooltip: '两边数字特效' })
    numberEffect: Node = null;
    @property(Node)
    btnIdleEffect: Node = null;
    @property(Node)
    turboEffectNode: Node = null;
    @property([Node])
    doorEffectNodes: Node[] = [];

    private _btnStartTw: Tween<Node> = null;
    public winTw: Tween<any> = null;
    public scoreTw: Tween<any> = null;

    public setStartAni(delayTime) {
        this._btnStartTw && (this._btnStartTw.stop(), this._btnStartTw = null);
        this._btnStartTw = tween(this.startRefresh).
            by(delayTime, { eulerAngles: new Vec3(0, 0, -360) })
            .repeatForever();
        this._btnStartTw.start();
    }

    public updateScore(startNum: number, endNum: number) {
        const param: IIncresaseData = {
            timer: 1
        }
        this.scoreTw?.removeSelf();
        this.scoreTw = KylinUtils.instance.IncreaseNumber(this.scoreText, startNum, endNum, param);
        this.scoreTw.start();
    }

    public updateWin(winScore: number) {
        const param: IIncresaseData = {
            timer: 0.5,
            tag: 3
        }
        this.winTw?.removeSelf();
        this.winTw = KylinUtils.instance.IncreaseNumber(this.winText, 0, winScore, param);
        this.winTw.start();
    }

    public entryMiniGame(isMini: boolean) {
        if (isMini) this.turntableBg.spriteFrame = this.turntableFrames[1];
        else this.turntableBg.spriteFrame = this.turntableFrames[0];
    }

    //初始化云朵
    public initCloud() {
        const cloudCount = randomRangeInt(3, 8);
        for (let i = 0; i < cloudCount; i++) {
            const cloud = KylinPoolManager.instance.getPoolObj(KylinPoolManager.PoolsName.cloud);
            cloud.parent = this.topBg;
            cloud.getComponent(Sprite).spriteFrame = this.cloudFrames[randomRangeInt(0, 5)];
            // const _x = -500 + random() * 1000;
            const _x = -500 - Math.random() * 100;
            const _y = 870 + random() * 920;
            cloud.setPosition(_x, _y);
            tween(cloud).to(randomRangeInt(60, 80), { position: new Vec3(1000 * _x / Math.abs(_x) * (-1), _y) }).call((node: Node) => {
                KylinPoolManager.instance.putPoolObj(KylinPoolManager.PoolsName.cloud, node);
            }).start();
        }
    }

    public playStartEffect() {
        this.startEffectAfter.play();
        this.startEffectFront.play();
    }
}


