import { instantiate } from 'cc';
import { Vec3 } from 'cc';
import { Prefab } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { KylinGameElement } from './KylinGameElement';
import { KylinGameManager } from './KylinGameManager';
import { SlotKylinBody, SlotKylinLine } from '../data/KylinGameData';
import { macro } from 'cc';
import { find } from 'cc';
import { Label } from 'cc';
import { tween } from 'cc';
import { KylinAnimationManager } from './KylinAnimationManager';
import { Font } from 'cc';
import { IRollInfo } from '../SlotScript/Slot';
import { KylinUtils } from '../base/KylinUtils';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
const { ccclass, property } = _decorator;

const column = [3, 4, 3];
const maxPos = [-112, -112, -112];
const posInterval = [232, 219, 232];
//232
const lineScorePos = [new Vec3(0, 780), new Vec3(0, 555), new Vec3(0, 330), new Vec3(0, 105)];
//最终奖品管理
@ccclass('KylinPrizeManager')
export class KylinPrizeManager extends Component {
    @property(Node)
    maskNode: Node = null;
    @property(Node)
    letfLightNode: Node = null;
    @property(Node)
    rightLightNode: Node = null;
    @property(Node)
    letfEffectNode: Node = null;
    @property(Node)
    rightEffectNode: Node = null;
    @property(Node)
    lineNode: Node = null;
    @property(Node)
    endContent: Node = null;
    @property(Node)
    prizeContent: Node = null;
    @property(Prefab)
    elementItem: Prefab = null;
    @property(Node)
    lineScoreNode: Node = null;
    @property(Label)
    lineScoreText: Label = null;
    @property(Node)
    prizeTextContent: Node = null;
    @property([Font])
    lineFont: Font[] = [];

    private static _instance: KylinPrizeManager = null;
    public static get instance() {
        return this._instance;
    }

    private _lineIndex = 0; //线路中奖时轮播下标
    private _lines: SlotKylinLine[] = []; //所有中奖的线
    private _scoreElements: Node[] = [];

    protected onLoad(): void {
        if (KylinPrizeManager._instance) {
            this.node.destroy();
            return;
        }

        KylinPrizeManager._instance = this;
        this.gameSetup();
    }

    private gameSetup() {
        for (let i = 0; i < this.endContent.children.length; i++) {
            for (let j = 0; j < column[i]; j++) {
                const element = instantiate(this.elementItem);
                element.parent = this.endContent.children[i];
                element.position = new Vec3(0, -112 - posInterval[i] * j);
                element.name = j.toString();
            }

            for (let j = 0; j < column[i]; j++) {
                const element = instantiate(this.elementItem);
                element.parent = this.prizeContent.children[i];
                element.position = new Vec3(0, -112 + posInterval[i] + posInterval[i] * j);
                element.name = j.toString();
                element.active = false;
            }
        }

        this.endContent.active = false;
        this.prizeContent.active = false;
        this.showPrize(KylinGameManager.instance.getFrameIds(), 0, 0);
    }

    protected onDestroy(): void {
        KylinPrizeManager._instance = null;
    }

    public showPrize(res: IRollInfo[][], prizeType: number, betScore: number, mainRes: SlotKylinBody = null, callback: Function = null) {
        this._lines = mainRes?.lines;
        this.endContent.active = true;
        for (let i = 0; i < res.length; i++) {
            for (let j = 0; j < res[i].length; j++) {
                const element = this.endContent.children[i].children[j].getComponent(KylinGameElement);
                element.initElement(res[i][j].name, res[i][j].text);
                element.node.active = true;
                element.getComponent(KylinGameElement).setSpine('', true);
                if (res[i][j].name === '301' && !KylinGameManager.instance.isAuto && !KylinGameManager.instance.isEntryMiniGame) element.showScoreEffect();

                const element2 = this.prizeContent.children[i].children[j].getComponent(KylinGameElement);
                element2.initElement(res[i][j].name, res[i][j].text);
                element2.showSpine(res[i][j].name);
            }
        }

        if (prizeType === 1) {
            //线路中奖
            this.linePrize();
            callback && callback();
        } else if (prizeType === 2) {
            //积分中奖
            this.scorePrize(mainRes, betScore, callback);
        } else {
            callback && callback();
        }
    }

    public showElementSpine(info: IRollInfo, index: number, j: number) {
        const element = this.endContent.children[index].children[j].getComponent(KylinGameElement);
        element.node.active = true;
        element.node.position = new Vec3(0, -112 - posInterval[index] * j);
        element.initElement(info.name, info.text, '3');
    }

    //线路中奖
    private linePrize() {
        this.prizeContent.active = true;
        this.closeAllLine();
        this._lineIndex = this._lines.length;
        this.maskNode.active = true;
        if(this._lines.length === 1) AudioMgr.inst.playOneShot("audio/AC_sound_8001_slotqilin_connect_single", 1, ModuleDef.SLOTS_KYLIN);
        else AudioMgr.inst.playOneShot("audio/AC_sound_8001_slotqilin_connect_multiple", 1, ModuleDef.SLOTS_KYLIN);
        this.schedule(this.carouselLine.bind(this), 1.5, macro.REPEAT_FOREVER, .1);
    }

    //积分中奖
    public scorePrize(res: SlotKylinBody, betScore: number, callback: Function = null) {
        if (!res.winScore) {
            callback && callback();
            return;
        }
        this._scoreElements = [];
        this.prizeContent.active = true;
        this.closeAllLine();
        this.maskNode.active = true;
        for (let i = 0; i < res.colums.length; i++) {
            for (let j = 0; j < res.colums[i].elements.length; j++) {
                const data = res.colums[i].elements[j];
                if (data.cardScore) {
                    const element = this.prizeContent.children[i].children[j];
                    element.active = true;
                    element.position = new Vec3(0, maxPos[i] - posInterval[i] * j);
                    this._scoreElements.push(element);
                }
            }
        }

        AudioMgr.inst.playOneShot("audio/AC_sound_8001_slotqilin_card_lightmore", 1, ModuleDef.SLOTS_KYLIN);
        if (this._scoreElements.length > 0 && !KylinGameManager.instance.isEntryMiniGame) {
            KylinAnimationManager.instance.playAni(KylinAnimationManager.instance.KylinAniName.happy, false);
        }
        for (let i = 0; i < this._scoreElements.length; i++) {
            this._scoreElements[i].getComponent(KylinGameElement).prizeEffect(true);
            tween(this._scoreElements[i]).delay(0.2 + i * 0.5)
                .call((node: Node) => {
                    if (i === this._scoreElements.length - 1) node.getComponent(KylinGameElement).addText(this.prizeTextContent, betScore, callback);
                    else node.getComponent(KylinGameElement).addText(this.prizeTextContent, betScore);
                })
                //     .to(0.1, { scale: new Vec3(1.2, 1.2) })
                .start();
        }
    }

    //轮播中奖线路
    private carouselLine() {
        this.resetPrizeElement();
        this.closeAllLine();
        if (this._lines.length === 1) this._lineIndex = 0;
        this.showEndContent();
        if (this._lineIndex === this._lines.length) {
            //显示所有线路
            if (!KylinGameManager.instance.isShowResult) AudioMgr.inst.playOneShot("audio/AC_sound_8001_slotqilin_connect_multiple", 1, ModuleDef.SLOTS_KYLIN);
            this.lineScoreNode.active = false;
            for (let i = 0; i < this._lines.length; i++) {
                const singleLine = this._lines[i];
                find(singleLine.lineNo.toString(), this.lineNode).active = true;
                const leftLab = find(singleLine.lineNo.toString(), this.letfLightNode).getComponent(Label);
                const rightLab = find(singleLine.lineNo.toString(), this.rightLightNode).getComponent(Label);
                leftLab.font = this.lineFont[0];
                rightLab.font = this.lineFont[0];
                leftLab.spacingX = 0;
                rightLab.spacingX = 0;
                // find(singleLine.lineNo.toString(), this.letfEffectNode).active = true;
                // find(singleLine.lineNo.toString(), this.rightEffectNode).active = true;
                for (let j = 0; j < singleLine.line.length; j++) {
                    const element = this.prizeContent.children[j].children[singleLine.line[j]];
                    const element2 = this.endContent.children[j].children[singleLine.line[j]];
                    element2.active = false;
                    element.active = true;
                    element.position = new Vec3(0, maxPos[j] - posInterval[j] * singleLine.line[j]);
                    element.getComponent(KylinGameElement).prizeEffect(false);
                    element.getComponent(KylinGameElement).setSpine('1', false);
                }
            }
        } else {
            if (!KylinGameManager.instance.isShowResult) AudioMgr.inst.playOneShot("audio/AC_sound_8001_slotqilin_connect_single", 1, ModuleDef.SLOTS_KYLIN);
            const singleLine = this._lines[this._lineIndex];
            find(singleLine.lineNo.toString(), this.lineNode).active = true;
            const leftLab = find(singleLine.lineNo.toString(), this.letfLightNode).getComponent(Label);
            const rightLab = find(singleLine.lineNo.toString(), this.rightLightNode).getComponent(Label);
            leftLab.font = this.lineFont[0];
            rightLab.font = this.lineFont[0];
            leftLab.spacingX = 0;
            rightLab.spacingX = 0;
            // find(singleLine.lineNo.toString(), this.letfEffectNode).active = true;
            // find(singleLine.lineNo.toString(), this.rightEffectNode).active = true;
            this.lineScoreNode.active = true;
            this.lineScoreText.string = KylinUtils.instance.changeString((singleLine.lineScore).toFixed(2));
            this.lineScoreNode.position = lineScorePos[singleLine.line[1]];
            for (let j = 0; j < singleLine.line.length; j++) {
                const element = this.prizeContent.children[j].children[singleLine.line[j]];
                const element2 = this.endContent.children[j].children[singleLine.line[j]];
                element2.active = false;
                element.active = true;
                element.position = new Vec3(0, maxPos[j] - posInterval[j] * singleLine.line[j]);
                element.getComponent(KylinGameElement).prizeEffect(false);
                element.getComponent(KylinGameElement).setSpine('1', false);
            }
        }

        this._lineIndex++;
        if (this._lineIndex > this._lines.length) this._lineIndex = 0;
    }

    public showEndContent(isActive = true) {
        for (let i = 0; i < this.endContent.children.length; i++) {
            for (let j = 0; j < column[i]; j++) {
                const element = this.endContent.children[i].children[j];
                element.getComponent(KylinGameElement).setSpine('', false);
                element.active = isActive;
            }
        }
    }

    //重设中奖元素位置
    private resetPrizeElement() {
        for (let i = 0; i < this.prizeContent.children.length; i++) {
            for (let j = 0; j < column[i]; j++) {
                const element = this.prizeContent.children[i].children[j];
                element.position = new Vec3(0, maxPos[i] + posInterval[i] + posInterval[i] * j);
                element.active = false;
            }
        }
    }

    public closeAllLine() {
        for (let i = 0; i < this.lineNode.children.length; i++) {
            this.lineNode.children[i].active = false;
        }

        for (let i = 0; i < this.letfLightNode.children.length; i++) {
            const leftLab = this.letfLightNode.children[i].getComponent(Label);
            const rightLab = this.rightLightNode.children[i].getComponent(Label);
            leftLab.font = this.lineFont[1];
            rightLab.font = this.lineFont[1];
            leftLab.spacingX = -15;
            rightLab.spacingX = -15;
            // this.letfEffectNode.children[i].active = false;
            // this.rightEffectNode.children[i].active = false;
        }
    }

    public hideEndContet() {
        this.resetPrizeElement();
        this.closeAllLine();
        this.lineScoreNode.active = false;
        this.endContent.active = false;
        this.prizeContent.active = false;
        this.maskNode.active = false;
        this.unscheduleAllCallbacks();
    }
}


