import { find } from 'cc';
import { sp } from 'cc';
import { tween } from 'cc';
import { Vec3 } from 'cc';
import { Label } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { UIKylinResult } from './UIKylinResult';
import { KylinUtils } from '../../base/KylinUtils';
import { UIOpacity } from 'cc';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
const { ccclass, property } = _decorator;

@ccclass('UIKylinResultItem')
export class UIKylinResultItem extends Component {
    @property(sp.Skeleton)
    boxSp: sp.Skeleton = null;
    @property(Label)
    winLab: Label = null;
    @property(Node)
    effectNode: Node = null;
    @property
    panelLevel: number = -1; //-1最大的页面等级

    private _tw = null;
    private _betScore = 0;
    private _winScore = 0;

    public show(betScore: number, winScore: number, startBet: number) {
        AudioMgr.inst.playOneShot("audio/AC_sound_8001_slotqilin_score_treasure_001", 1, ModuleDef.SLOTS_KYLIN);
        this.boxSp.setAnimation(0, 'animation', true);
        this._betScore = betScore;
        this._winScore = winScore;
        this.node.active = true;
        let maxBet = 0;
        let level = 0;
        if (this.panelLevel === 20) {
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === 100) {
            level = 20;
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === -1) {
            level = 100;
            maxBet = winScore;
        }
        this.winLab.string = KylinUtils.instance.changeString(startBet.toFixed(2));
        if (this.panelLevel === 20) {
            startBet = 0;
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === 100) {
            startBet = betScore * 20;
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else {
            startBet = betScore * 100;
            maxBet = winScore;
        }
        const cb = () => {
            if (winScore > maxBet) {
                UIKylinResult.instance.showNext(this.panelLevel, betScore, winScore);
            } else {
                this.scheduleOnce(() => {
                    UIKylinResult.instance.hide();
                }, 2);
                UIKylinResult.instance.stopOneShot();
                UIKylinResult.instance.isNext = true;
                UIKylinResult.instance.isBest = true;
            }
        }

        this.node.getComponent(UIOpacity).opacity = 0;
        this.node.scale = new Vec3(1, 0, 1);
        tween(this.node.getComponent(UIOpacity)).to(0.3, { opacity: 255 }).call(() => {
            this._tw = KylinUtils.instance.IncreaseNumber(this.winLab, startBet, maxBet, { timer: 2.6, callback: cb.bind(this) });
            this._tw.start();
        }).start();
        tween(this.node).to(0.3, { scale: Vec3.ONE }).start();
    }

    public show2(betScore: number, winScore: number, startBet: number) {
        this.boxSp.setAnimation(0, 'animation', true);
        this._betScore = betScore;
        this._winScore = winScore;
        this.node.active = true;
        let maxBet = 0;
        let level = 0;
        if (this.panelLevel === 20) {
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === 100) {
            AudioMgr.inst.playOneShot("audio/AC_sound_8001_slotqilin_score_treasure_002", 1, ModuleDef.SLOTS_KYLIN);
            level = 20;
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === -1) {
            AudioMgr.inst.playOneShot("audio/AC_sound_8001_slotqilin_score_treasure_003", 1, ModuleDef.SLOTS_KYLIN);
            level = 100;
            maxBet = winScore;
        }

        this.winLab.string = KylinUtils.instance.changeString((startBet).toFixed(2));
        if (this.panelLevel === 20) {
            // startBet = 0;
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === 100) {
            // startBet = betScore * 20;
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else {
            // startBet = betScore * 100;
            maxBet = winScore;
        }
        const cb = () => {
            if (winScore > maxBet) {
                UIKylinResult.instance.showNext(this.panelLevel, betScore, winScore);
            } else {
                this.scheduleOnce(() => {
                    UIKylinResult.instance.hide();
                }, 2);
                UIKylinResult.instance.stopOneShot();
                UIKylinResult.instance.isNext = true;
                UIKylinResult.instance.isBest = true;
            }
        }

        this.effectNode.getComponent(UIOpacity).opacity = 0;
        this.effectNode.scale = new Vec3(1, 0, 1);
        tween(this.effectNode.getComponent(UIOpacity)).to(0.3, { opacity: 255 }).start();
        tween(this.effectNode).to(0.3, { scale: Vec3.ONE }).start();

        this._tw = KylinUtils.instance.IncreaseNumber(this.winLab, startBet, maxBet, { timer: 2.1, callback: cb.bind(this) });
        this._tw.start();
    }

    public setWinScore(winScore: number) {
        this.boxSp.setAnimation(0, 'animation', true);
        this.node.active = true;
        this.winLab.string = KylinUtils.instance.changeString((winScore).toFixed(2));
    }

    public unAllCallbacks() {
        this.unscheduleAllCallbacks();
        let maxBet = this._betScore * this.panelLevel;
        maxBet = this._winScore >= maxBet ? maxBet : this._winScore;
        if (this.panelLevel === -1) maxBet = this._winScore;
        this.winLab.string = KylinUtils.instance.changeString((maxBet).toFixed(2));
        if (this._winScore <= maxBet) {
            this.scheduleOnce(() => {
                UIKylinResult.instance.hide();
            }, 2);
        }
        this._tw && (this._tw.stop(), this._tw = null);
    }

    public hide() {
        this.unscheduleAllCallbacks();
        this._tw && (this._tw.stop(), this._tw = null);
        this.node.active = false;
    }
}


