import { Vec3 } from 'cc';
import { Label } from 'cc';
import { Tween } from 'cc';
import { Sprite } from 'cc';
import { Widget } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { KylinUtils } from '../../base/KylinUtils';
import { SpriteFrame } from 'cc';
import { UIKylinResultItem } from './UIKylinResultItem';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
const { ccclass, property } = _decorator;

const multLevel = [20, 100];
@ccclass('UIKylinResult')
export class UIKylinResult extends Component {
    @property(UIKylinResultItem)
    bitWin: UIKylinResultItem = null;
    @property(UIKylinResultItem)
    mageWin: UIKylinResultItem = null;
    @property(UIKylinResultItem)
    superWin: UIKylinResultItem = null;
    @property(Label)
    winLab: Label = null;

    private static _instance: UIKylinResult = null;
    public static get instance() {
        return this._instance;
    }

    private _winScore: number = 0;
    private _level = 20;
    private _betScore = 0;

    public isNext = false;
    public isBest = false;
    private _callback: Function = null;
    private _mult = 0;
    private _collectId = -1; //collect音效id

    protected onLoad() {
        if (UIKylinResult._instance) {
            this.node.destroy();
            return;
        }

        UIKylinResult._instance = this;
        const widget = this.node.getComponent(Widget);
        widget.left = 0;
        widget.right = 0;
        this.node.position = Vec3.ZERO;
        // this.node.active = false;
        this.node.on(Node.EventType.TOUCH_END, this.onBtnNext, this);
        this.hide();
    }

    protected onDestroy(): void {
        UIKylinResult._instance = null;
    }

    public showResult(winScore: number, betScore: number, callback: Function) {
        //展示结算界面
        this._mult = winScore / betScore;
        this._callback = callback;
        this._level = 20;
        this.isNext = false;
        this.isBest = false;
        this._winScore = winScore ? winScore : 0;
        this._betScore = betScore;
        if (!this._mult || this._mult < 10) return this._callback && (this._callback(), this._callback = null);
        this.node.active = true;
        AudioMgr.inst.play("audio/AC_sound_8001_music_slotqilin_interface_003", 1, ModuleDef.SLOTS_KYLIN);
        AudioMgr.inst.playOneShot("audio/AC_sound_8001_slotqilin_number_collect", 1, ModuleDef.SLOTS_KYLIN).then(aeid => this._collectId = aeid);
        if (this._mult >= 10) {
            this.winLab.string = KylinUtils.instance.changeString("0.00");
            this.bitWin.show(betScore, winScore, 0);
        }
    }

    public showNext(level: number, betScore: number, winScore: number) {
        //展示下一个等级界面界面
        this.isNext = false;
        if (level === 20) {
            this._level = 100;
            this.bitWin.hide();
            this.mageWin.show2(betScore, winScore, 20 * betScore);
        } else if (level === 100) {
            this._level = -1;
            this.mageWin.hide();
            this.superWin.show2(betScore, winScore, 100 * betScore);
        }
    }

    public onBtnNext() {
        //点击展示下一个等级界面，或者点击后直接展示当前等级的最终滚动值
        if (this.isBest) {
            this.hide();
            return;
        }
        if (this._collectId != -1) (AudioMgr.inst.stopOneShot(this._collectId, "audio/AC_sound_8001_slotqilin_number_collect", ModuleDef.SLOTS_KYLIN), this._collectId = -1);
        this.bitWin.unAllCallbacks();
        this.mageWin.unAllCallbacks();
        this.superWin.unAllCallbacks();
        this.bitWin.hide();
        this.mageWin.hide();
        this.superWin.hide();

        if (this._mult >= 10 && this._mult <= 20) {
            this.bitWin.setWinScore(this._winScore);
        } else if (this._mult > 20 && this._mult <= 100) {
            this.mageWin.setWinScore(this._winScore);
        } else {
            this.superWin.setWinScore(this._winScore);
        }

        this.isBest = true;
        this.scheduleOnce(() => {
            this.hide();
        }, 2)
    }

    private toNext() {
        this.showNext(this._level, this._betScore, this._winScore);
    }

    protected onDisable(): void {
        this.hide();
    }

    public hide() {
        if (this._collectId != -1) (AudioMgr.inst.stopOneShot(this._collectId, "audio/AC_sound_8001_slotqilin_number_collect", ModuleDef.SLOTS_KYLIN), this._collectId = -1);
        this.unscheduleAllCallbacks();
        this.onBtnClose();
        this.isNext = false;
        this.isBest = false;
        this.bitWin.hide();
        this.mageWin.hide();
        this.superWin.hide();
        this._callback && (this._callback(), this._callback = null);
        // SlotUnicorn.UIManager.instance.resetGame();
        // SlotUnicorn.UIManager.instance.setScore(this._winScore);
    }

    stopOneShot() {
        if (this._collectId != -1) (AudioMgr.inst.stopOneShot(this._collectId, "audio/AC_sound_8001_slotqilin_number_collect", ModuleDef.SLOTS_KYLIN), this._collectId = -1);
    }

    private onBtnClose() {
        this.node.active = false;
    }
}


