import { instantiate } from 'cc';
import { tween } from 'cc';
import { Enum } from 'cc';
import { randomRangeInt } from 'cc';
import { Vec3 } from 'cc';
import { sp } from 'cc';
import { find } from 'cc';
import { Prefab } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
const { ccclass, property } = _decorator;

enum KylinAniStatus {
    appear,
    idle,
    happy,
    leave,
    performance,
    shake
}
Enum(KylinAniStatus);

const KylinAniName = {
    appear: 'appear',
    idle: 'idle',
    happy: 'happy',
    leave: 'leave',
    performance: 'performance',
    shake: 'shake'
}

/**
 * 麒麟动画管理类
 */
@ccclass('KylinAnimationManager')
export class KylinAnimationManager extends Component {
    private static _instance: KylinAnimationManager = null;
    public static get instance() {
        return this._instance;
    }

    public KylinAniName = KylinAniName;
    private _status: KylinAniStatus = KylinAniStatus.idle;
    private aniListeners = {};
    private _isPlayLoop = false;
    private _lastVoice = 0;
    private _playNum = 0;
    private _playRanNum = 0;

    protected onLoad(): void {
        if (KylinAnimationManager._instance) {
            this.node.destroy();
            return;
        }

        this._playRanNum = randomRangeInt(3, 6);
        KylinAnimationManager._instance = this;
        this.gameSetup();
    }

    private gameSetup() {
        // root.game.bundle.preloadDir('prefabs/kylin', Prefab, (err: Error) => {
        //     if (err) return console.error(err);
        // });
    }

    protected onDestroy(): void {
        KylinAnimationManager._instance = null;
    }

    public playAni(aniName: string, isLoop: boolean) {
        if (this._status === KylinAniStatus[aniName]) return;
        if (aniName === KylinAniName.performance) this.node.setSiblingIndex(3);
        else this.node.setSiblingIndex(1);
        this.closeAllAni();
        this._isPlayLoop = isLoop;
        this._status = KylinAniStatus[aniName];
        let aniNode = find(aniName, this.node);
        if (!aniNode) {
            // root.game.bundle.load('prefabs/kylin/' + aniName, Prefab, (err: Error, prefab: Prefab) => {
            //     if (err) console.error(`加载${aniName}失败。`, err);
            //     aniNode = instantiate(prefab);
            //     aniNode.parent = this.node;
            //     aniNode.getComponent(sp.Skeleton).setAnimation(0, 'animation', isLoop);
            //     if (!this.aniListeners[aniName] && aniName != KylinAniName.performance) {
            //         this.aniListeners[aniName] = aniName;
            //         this.aniListener(aniNode.getComponent(sp.Skeleton));
            //     }
            // });
            return;
        }
        if (aniName === 'appear') {
            this.scheduleOnce(() => {
                AudioMgr.inst.playOneShot("audio/AC_sound_8001_slotqilin_voice_001", 1, ModuleDef.SLOTS_KYLIN);
            }, 1.7);
        } else if (aniName != "idle") {
            if (this._playNum >= this._playRanNum) {
                this._playNum = 0;
                this._playRanNum = randomRangeInt(3, 6);
            } else {
                this._playNum++;
                let voice = randomRangeInt(2, 7);
                while (this._lastVoice === voice) {
                    voice = randomRangeInt(2, 7);
                }
                const path = "audio/AC_sound_8001_slotqilin_voice_00" + voice;
                AudioMgr.inst.playOneShot(path, 1, ModuleDef.SLOTS_KYLIN);
            }
        }
        aniNode.active = true;
        aniNode.getComponent(sp.Skeleton).setAnimation(0, 'animation', isLoop);
        if (!this.aniListeners[aniName] && aniName != KylinAniName.performance) {
            this.aniListeners[aniName] = aniName;
            this.aniListener(aniNode.getComponent(sp.Skeleton));
        }
    }

    private aniListener(ani: sp.Skeleton) {
        ani.setCompleteListener((ani) => {
            if (this._isPlayLoop) return;
            this.playAni(KylinAniName.idle, true);
        });
    }

    public setAniPosition(isBig: boolean) {
        if (isBig) {
            tween(this.node).to(1.5, { position: new Vec3(223, 543) }).start();
        } else {
            tween(this.node).to(1.5, { position: new Vec3(217, 474) }).start();
        }
    }

    public setAniPositon2(pos: Vec3) {
        this.node.position = pos;
        // tween(this.node).to(0.2, { position: pos }).start();
    }

    public fakeMiniGame() {
        tween(this.node).to(1.5, { position: new Vec3(217, 474) })
            .delay(1)
            .to(1.5, { position: new Vec3(223, 543) })
            .start();
    }

    public closeAllAni() {
        for (let i = 0; i < this.node.children.length; i++) {
            this.node.children[i].active = false;
        }
    }
}


